'use client';

import React, { useState, useEffect } from 'react';

interface FloatingDotsProps {
  count?: number;
  sizeRange?: [number, number];
  speedRange?: [number, number];
}

interface Dot {
  id: number;
  style: React.CSSProperties;
}

export function FloatingDots({
  count = 50,
  sizeRange = [1, 5],
  speedRange = [10, 30],
}: FloatingDotsProps) {
  const [dots, setDots] = useState<Dot[]>([]);

  useEffect(() => {
    const generateDots = () => {
      const newDots: Dot[] = [];
      for (let i = 0; i < count; i++) {
        const size = Math.random() * (sizeRange[1] - sizeRange[0]) + sizeRange[0];
        const speed = Math.random() * (speedRange[1] - speedRange[0]) + speedRange[0];
        const delay = Math.random() * speedRange[1];

        newDots.push({
          id: i,
          style: {
            left: `${Math.random() * 100}vw`,
            top: `${Math.random() * 100}vh`,
            width: `${size}px`,
            height: `${size}px`,
            animationDuration: `${speed}s`,
            animationDelay: `${-delay}s`,
            opacity: 0,
          },
        });
      }
      setDots(newDots);
    };

    generateDots();
    // No need to regenerate on resize for this implementation, dots are vw/vh based.
  }, [count, sizeRange, speedRange]);

  return (
    <div className="fixed top-0 left-0 w-full h-full pointer-events-none -z-10">
      {dots.map(dot => (
        <div key={dot.id} className="dot" style={dot.style} />
      ))}
    </div>
  );
}
